@import "../../../css/module/fn";


.filterBar {
	padding: 0 p2m(40);
	box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.1);
	border-radius: 6px;
	
	.mui-btn-block {
		font-size: p2m(22);
		padding: 8px 0;
	}
	
	#selDate {
		width: p2m(250);
	}
	#selMainCatagory {
		width: p2m(200);
	}
	#selSubCatagory {
		width: p2m(150);
	}
}

.showcase {
	box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.1);
	margin-top: p2m(10);
	border-radius: 6px;
	padding: p2m(30);
	
	position: absolute;
	top: p2m(330);
	left: 50%;
	margin-left: p2m(-350);
	bottom: p2m(36);
	//overflow: hidden;
	/**/
	#myScroll{
		position: absolute;
		top: p2m(100);
		left: 0;
		bottom: p2m(36);
		overflow: auto;
	}
	
	.mui-table-view {
		&:before, &:after {
			background-color: transparent;
		}
	}
	img {
		//max-width: p2m(300) !important;
		//max-height: p2m(300) !important;
	}
}

#fullSizeViewer {
	
	display: none;
	
	.fullSizerViewerWrapper{
		display:-webkit-box;
		-webkit-box-pack:center;
		-webkit-box-align:center;
		height: 100%;
	}
	
	
	background-color: rgba(0, 0, 0, 0.85);
	text-align: center;
	/*padding: 0 0.3rem;*/
	position: fixed;
	z-index: 1001;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	
	#closer {
		position: absolute;
		color: #fff;
		font-size: 1rem;
		top: 0.5rem;
		right: 0.5rem;
	}
	
	#divImgFullSize {
		margin: 2.6rem auto 0;
		width: 9.5rem;
		height: 9.5rem;
		border: none;
		
		#imgFullSize {
			max-height: 100%;
			max-width: 100%;
			border:3px solid #fff
		}
	}
}


